.nbbtn-container {
  position: fixed;
  bottom: 20px;
  left: 40px;
  z-index: 11;
  opacity: 0.8;
  cursor: pointer;
  text-align: center;
  -webkit-transition: all 0.25s ease;
  transition: all 0.25s ease;

  &:hover {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
    i {
      animation: roll 3s infinite;
    }
  }

  div {
    font-weight: bold;
    color: rgb(3, 209, 164);
  }

  .btn {
    color: white;
    background-image: linear-gradient(107deg, rgba(13, 198, 180, 1) 8.1%, rgba(33, 198, 138, 1) 79.5%);
  }
}
.modal-new {
  label {
    font-weight: bold;
    font-size: 16px;
    margin-top: 0.5rem;
  }
  .ant-form-item {
    margin-bottom: 0;
  }
  .ant-btn-lg {
    text-transform: uppercase;
    background-image: linear-gradient(107deg, rgba(13, 198, 180, 1) 8.1%, rgba(33, 198, 138, 1) 79.5%);
    border-color: #47eeca;

    &:hover,
    &:active,
    &:focus {
      border-color: #47eeca;
    }
  }

  .avatar-uploader > .ant-upload,
  .img-lightbox {
    width: 128px;
    height: auto;
  }

  .mask:hover {
    cursor: zoom-in;
  }

  .modal-header {
    background-image: linear-gradient(107deg, rgba(13, 198, 180, 1) 8.1%, rgba(33, 198, 138, 1) 79.5%);
  }
  .btn-outline-default {
    border: 2px solid #0cd4a9 !important;
    color: #0cd4a9 !important;
    padding-left: 20px;
    padding-right: 20px;
  }
  .btn-default {
    background-image: linear-gradient(107deg, rgba(13, 198, 180, 1) 8.1%, rgba(33, 198, 138, 1) 79.5%);
  }
}
@keyframes roll {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
